.file-div {

    background-color: #288FDE;
    width: 100px;
    position: relative;
    border: solid;
    text-align: center;
    -webkit-transition-duration: 0.4s; /* Safari */
    transition-duration: 0.4s;
    text-decoration: none;
    overflow: hidden;
    cursor: pointer;
    border-radius: 20px;
    /* padding-right: 2px; */
    margin-top: 2px;
    margin-left: 30px;
}

/* .uploadLabel {
    padding-left: 10px;
} */

.file-input {
    display: none;
}

  
  .uploadLabel:after {
    content: "";
    background: #1d6ba7;
    display: block;
    position: absolute;
    padding-top: 300%;
    padding-left: 350%;
    margin-left: -20px!important;
    margin-top: -120%;
    opacity: 0;
    transition: all 0.8s
  }
  
  .uploadLabel:active:after {
    padding: 0;
    margin: 0;
    opacity: 1;
    transition: 0s
  }


